
@charset "UTF-8";
@import url(./assets/css/common.css);
.h25 {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  height: 25px;
  width: 100%;
}

/* 水波纹样式 */
span {
  position: relative;
  display: block;
  height: 3em;
  letter-spacing: .2em;
  font-weight: bold;
  cursor: pointer;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 2px;
}
.ripple {
  position: absolute;
  background: rgba(0, 0, 0, 0.25);
  border-radius: 100%;
  pointer-events: none;
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
  -o-transform: scale(0);
  -ms-transform: scale(0);
  z-index: 9999;
}
.ripple.show {
    animation: ripple .75s ease-out;
    -moz-animation: ripple .75s ease-out;
    -webkit-animation: ripple .75s ease-out;
    -o-animation: ripple .75s ease-out;
    -ms-animation: ripple .75s ease-out;
}
@keyframes ripple {
to {
    transform: scale(2);
    -moz-transform: scale(2);
    -webkit-transform: scale(2);
    -o-transform: scale(2);
    -ms-transform: scale(2);
    opacity: 0;
}
}
.skinA .h25 {
  background: #007aff;
}
.skinB .h25 {
  background: #31c3b0;
}
.skinC .h25 {
  background: #2f354a;
}



